.pagination {
  display: flex;
  justify-content: end;
  gap: 1px;

  &__complete {
    display: flex;
    align-items: end;
    justify-content: center;
    width: 56px;
    padding-bottom: 15px;
  }

  &__cell {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 56px;
  }

  &--disabled {
    background-color: #{$button-active} !important;
  }
}
